.header{
    position: fixed;
    min-height: 66px;
    width: 100%;
    /*background-color: #f5df4e;*/
    background-color: #fff;
    display: flex;
    align-items: center;
    z-index: 5;
    .wrapper {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        .userName {
            font-size: 2rem;
            margin: 0;
            text-transform: uppercase;
        }
    }
    @media screen and (max-width: 992px) {
        .wrapper{
            .userName {
                font-size: 1.6rem;
            }
        }
    }
    //@media screen and (max-width: 862px) {
    //    .wrapper{
    //        .userName {
    //            font-size: 1.2rem;
    //        }
    //    }
    //}
}
